<template>
  <div>
    <van-dialog
      :value="visbile"
      title=""
      @cancel="handlenCancel"
      @confirm="handlenConfirm"
      show-cancel-button
    >
      <div class="detail">
        <van-cell-group>
          <van-field label="" required v-model="value" placeholder="" />
          <van-field label="" required v-model="value" placeholder="" />
          <van-field label="" required v-model="value" placeholder="" />
          <van-field label="" required v-model="value" placeholder="" />
          <van-field label="" required v-model="value" placeholder="" />
          <van-field label="" required v-model="value" placeholder="" />
          <van-field label="" required v-model="value" placeholder="" />
          <van-field label=""  v-model="value" placeholder="" />
        </van-cell-group>
      </div>
    </van-dialog>
  </div>
</template>
<script>
export default {
  props: {
    visbile: {
      type: Boolean
    }
  },
  data(){
    return {
      value: ''
    }
  },
  methods: {
    handlenCancel(){
      this.$emit('handlenCancel')
    },
    handlenConfirm(){
      this.$emit('handlenConfirm')
    }
  }
}
</script>

<style lang="less" scoped>
.detail{
  padding: 0 20px;
}
</style>



