export const baseSize = 37.5

//  rem 
function setRem () {
 //  750 
 const scale = document.documentElement.clientWidth / (baseSize*20)
 // 
 document.documentElement.style.fontSize = (baseSize * Math.min(scale, 2)) + 'px'
}
// 
setRem()
//  rem
window.onresize = function () {
 setRem()
}


