        <van-step>
          <div></div>
          <span>2016-07-11 10:00</span>
        </van-step>
        <van-step>
          <div></div>
          <span>2016-07-10 09:30</span>
        </van-step>
        <van-step>
          <div></div>
          <span>2016-07-10 09:30</span>
        </van-step>
        <van-step>
          <div></div>
          <span>2016-07-10 09:30</span>
        </van-step>
      </van-steps>
    </div>
  </div>
</template>

<script>
import Header from "@/components/Header"

export default {
  components: {
    Header
  },
  data(){
    return {

    }
  }
}
</script>

<style lang="less" scoped>
.flow_chart {
  width: 100%;
  font-size: 28px;
}
.flow {
  padding-left: 100px;
  background-color: #fff;
}
</style>



