<template>
  <div class="joinsign">
    <Header :tabs="tabs" :tabActive="tabActive" @handlenSwitch="(val)=>tabActive=val" />
    <div>
      <van-cell-group>
        <van-field label="" placeholder="" />
        <van-field label="" placeholder="">
          <van-button slot="button" size="small" type="primary"></van-button>
        </van-field>
      </van-cell-group>
    </div>
    <div class="cellList">
      <Cell
        icon="icon iconfont icon-untreated"
        top=""
        right="Openlssue"
        bottom="2019-02-28"
        left=""
        link="/joinsign/detail"
      />
    </div>
  </div>
</template>

<script>
import Header from "@/components/Header"
import Cell from "@/components/Cell"

export default {
  components: {
    Header,
    Cell
  },
  data(){
    return {
      tabs: [
        {name: '', type: 1},
        {name: '', type: 0}
      ],
      tabActive:1,
    }
  }
}
</script>

<style lang="less" scoped>
.joinsign {
  width: 100%;
}
.cellList {
  margin-top: 20px;
}
</style>



